/* stylelint-disable at-rule-empty-line-before */

/* stylelint-disable at-rule-empty-line-before */

.try-page .description {
  margin: 0 auto;
  max-width: 25rem;
  text-align: center;
}

.try-page .error {
  background: #edd9d9;
  border-radius: 1px;
  margin-bottom: 3rem;
  padding: 1rem 1rem 1rem 2.5rem;
  position: relative;
}

.try-page .error > .icon {
  fill: #de3b3b;
  left: .75rem;
  position: absolute;
  top: 1.375rem;
}

.try-page .links {
  display: flex;
  justify-content: center;
}

.try-page .links > p:not(:last-child),
.try-page .links > form:not(:last-child) {
  margin-right: 5rem;
}

@media screen and (max-width: 44rem) {
  .try-page .links {
    flex-wrap: wrap;
  }

  .try-page .links > p,
  .try-page .links > form {
    flex-basis: 100%;
  }

  .try-page .links > p:not(:last-child),
  .try-page .links > form:not(:last-child) {
    margin-right: 0;
    margin-bottom: 2rem;
  }

  .try-page .links > .demo {
    order: -1;
  }
}

.try-page .links a,
.try-page .links button {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}

.try-page .links button {
  border: none;
  cursor: pointer;
  margin: 0 auto 1rem;
}

.try-page .links button[disabled] {
  background: #777;
  cursor: progress;
}

.try-page .starterkit {
  border: 1px solid #000;
  line-height: 0;
  margin: 0 auto 1.5rem;
  max-width: 30rem;
}




